import React, {Component} from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import {Link} from 'react-router-dom';
import {OutboundLink} from 'react-ga';

import './Welcome.css';

import issueScreenshot from './images/screenshot-issue.png';

const badgeMarkdown = '[![DepShield Badge](https://depshield.sonatype.org/badges/{owner}/{repository}/depshield.svg)](https://depshield.github.io)';

class Welcome extends Component {
  render() {
    return (
        <div className="row">
          <div className="col">
            <h1>Getting Started</h1>
            <p className="lead text-muted">
              Congratulations on installing Sonatype DepShield. DepShield is currently analyzing your subscribed
              repositories for vulnerabilities in their open source dependencies.
            </p>
            <div className="alert alert-warning" role="alert">
              Haven't installed DepShield yet? Browse to <OutboundLink to="//github.com/apps/sonatype-depshield"
                                                                       eventLabel="//github.com/apps/sonatype-depshield">DepShield
              in the GitHub Marketplace</OutboundLink> and follow the installation instructions.
            </div>
            <h2>DepShield Badge</h2>
            <p>
              DepShield provides a badge to indicate the state of components in your repository. This badge can be
              added to your README using Markdown using the code below, substituting <code>{'{owner}'}</code> with your
              GitHub username or organization and <code>{'{repository}'}</code> with your repository name.
            </p>
            <figure className="code">
              <CopyToClipboard text={badgeMarkdown}>
                <button type="button" className="btn btn-light copy">Copy</button>
              </CopyToClipboard>
              <pre>
                <code>
                  {badgeMarkdown}
                </code>
              </pre>
            </figure>
            <p>
              The badge added to your README will display the number of vulnerable components in your project along with
              a color indicating the severity of the vulnerability. Remediating the Issues generated by DepShield
              will reduce the number of vulnerabilities until there are none as indicated by the badge.
            </p>
            <h2>DepShield Issues</h2>
            <p>
              DepShield will generate GitHub Issues for any security vulnerability existing in a project's direct
              dependencies. The GitHub Issues may be remediated by the project maintainer and any badge information
              will be updated during the continuous monitoring process. The GitHub Issues will include a link with more
              information about the vulnerability to <OutboundLink to="//ossindex.sonatype.org"
                                                                   eventLabel="//ossindex.sonatype.org"
                                                                   target="_blank">Sonatype OSS Index</OutboundLink>.
              Note that registration to OSS Index is currently required to discover more details about a vulnerability.
              Check the <Link to="/roadmap">Road Map</Link> for details about supporting GitHub Authentication for OSS
              Index.
            </p>
            <p>
              <img src={issueScreenshot} className="img-fluid mx-auto d-block" width="600px" alt="DepShield Issue" />
            </p>
            <h2>Continuous Monitoring</h2>
            <p>
              DepShield will continue to monitor your project for changes and update your badge with the latest
              component information. New GitHub Issues will be created as vulnerabilities are discovered or new
              components are introduced that are vulnerable. DepShield is a new service and we are still working on the
              perfect balance between cost and value for project evaluations. If you are expecting a change, please be
              patient as it may take on the order of days.
            </p>
          </div>
        </div>
    );
  }
}

export default Welcome;



