import React, {Component} from 'react';
import { OutboundLink } from 'react-ga';

import npmLogo from './images/npm-128x128.png';
import pyPiLogo from './images/pypi-64x64.png';

class RoadMap extends Component {
  render() {
    return (
        <div className="row">
          <div className="col">
            <h1>Road Map</h1>
            <p className="text-muted">
              We fully plan on releasing road map functionality while understanding that attack by kraken or other more
              mundane changes to plans could cause reality to deviate from this road map. OSS Index and DepShield are
              both free services to provide security vulnerability data to developers and this underlying principal will
              drive all future road map decisions.
            </p>
            <h2>Language Ecosystems</h2>
            <p className="text-muted">
              Today's software is written in the language appropriate for the problem; no language is a one size
              fits all solution. Sonatype values and supports the breadth of software languages and will continue
              to invest in providing solutions for different ecosystems.
            </p>
            <ul className="list-unstyled">
              <li>
                <h4>
                  <span data-toggle="tooltip" data-placement="bottom" title=""
                        data-original-title="Node Package Manager">
                    <OutboundLink to="//ossindex.sonatype.org/ecosystem/npm"
                                  eventLabel="//ossindex.sonatype.org/ecosystem/npm" target="_blank">
                      <img className="mr-1" src={npmLogo} height="32" width="32"/> JavaScript (npm) Support
                    </OutboundLink>
                  </span>
                </h4>
              </li>
              <li>
                <h4>
                  <span data-toggle="tooltip" data-placement="bottom" title=""
                        data-original-title="Python Package Index">
                      <OutboundLink to="//ossindex.sonatype.org/ecosystem/pypi"
                                    eventLabel="//ossindex.sonatype.org/ecosystem/pypi" target="_blank">
                        <img className="mr-1" src={pyPiLogo} height="32" width="32"/> Python (PyPI) Support
                      </OutboundLink>
                    </span>
                </h4>
              </li>
            </ul>
            <h2>Usability</h2>
            <p className="text-muted">
              In order for DepShield to be useful it must be usable. Incremental improvements in usability will ensure
              the best experience for its users and keep them coming back. Building virtuous cycles in DepShield
              will allow us to fulfill our goal of reducing the security vulnerability footprint in Open Source
              Software.
            </p>
            <ul className="list-unstyled">
              <li>
                <h4>GitHub OAuth Login for OSS Index</h4>
              </li>
            </ul>
          </div>
        </div>
    );
  }
}

export default RoadMap;


